.header-color{
    background: #12c2e9;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #f64f59, #c471ed, #12c2e9);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right,rgb(203,52,181),rgb(68,166,187));
    
}


.landing-grid{
    text-align: center;
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.avatar-image{
    height: 400px;
    padding-top: 5em;   
}
.banner-text{
    background-color: black;
    opacity: 0.8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}
.banner-text h1 {
    font-size: 66px;
    font-weight: bold;
    color: white;
}
.banner-text hr{
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}

.banner-text p{
    color: white;
    font-size: 20px;
    padding: 1em;
}

.social-links{
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
}

.social-links i{
    color: white;
    font-size: 5em;
    padding: 8px;

}

.projects-grid{
    display: flex;
    margin: 10px;
    
}


.contact-body{
    margin:auto;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top:2em;  
}

.contact-grid{
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    color: black ;
    margin-bottom: 20px;
}

.contact-grid h2{
    font-family: 'Anton',sans-serif;
}

.contact-grid p{
    font-family: 'Oxygen',sans-serif;
    font-weight: bold; 
}

.contact-grid hr{
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
}

.contact-list i{
    font-size: 66px;
    padding-right: 5rem;
}



.resume-right-col{
    background:#27221F;
    color: white; 
    padding: 2em;
}

.particles{
    position: fixed;
    width: 100%;
    height: 100%;
}